<template>
	<gracePage :customHeader="false">
		<view slot="gBody" class="grace-body">
			<view class="grace-form grace-margin-top">
				<view class="grace-items">
					<text class="grace-icons icon-tel icon grace-blue"></text>
					<view class="grace-label">手机</view>
					<input type="number" class="input text-align-left" name="mobile" v-model="mobile" placeholder="请输入手机号码" />
					<view class="grace-form-funs grace-icons icon-close" @tap="mobile = ''" v-if="mobile.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-address grace-blue"></text>
					<text class="margin-right-ten"></text>
					<text class="margin-right-ten"></text>
					<view class="grace-label">邮箱</view>
					<input type="text" class="input text-align-left" name="email" v-model="email" placeholder="请输入邮箱" />
					<view class="grace-form-funs grace-icons icon-close" @tap="email = ''" v-if="email.length > 0"></view>
				</view>

				<view @tap="resetPassword"><button class="btn-submit" type="primary">立即找回</button></view>
			</view>
		</view>
	</gracePage>
</template>

<script>
export default {
	data() {
		return {
			mobile: '',
			email: ''
		};
	},
	onLoad: function() {
		var me = this;
		var userInfo = me.getCurrentUserInfo();
		if (userInfo !== null) {
			//未登录，返回登陆界面
			uni.reLaunch({
				url: 'usercenter'
			});
		}
	},
	components: {},
	methods: {
		initPosition() {
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		resetPassword() {
			if (!this.checkMobile(this.mobile)) {
				uni.showToast({
					title: '请输入正确手机号码',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			if (!this.checkEmail(this.email)) {
				uni.showToast({
					title: '请输入正确邮箱地址',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			uni.showLoading({
				title: '加载中...'
			});
			var me = this;
			var serverUrl = me.getServerUrl();
			// console.log(serverUrl);
			uni.request({
				url: serverUrl + '/User/ResetPassword?mobile=' + me.mobile + '&email=' + me.email,
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret
				},
				success: res => {
					if (res.data.toString().indexOf('Error') != -1) {
						uni.showToast({
							title: me.serverUrlName + '维护错误'
						});
						setTimeout(function() {
							uni.reLaunch({
								url: 'login'
							});
						}, 1000);
					} else {
						if (res.data.Status) {
							uni.showToast({
								title: '操作成功，请登录邮箱查收新密码！',
								duration: 2000,
								icon: 'none'
							});
							setTimeout(function() {
								uni.hideLoading();
								uni.reLaunch({
									url: 'login'
								});
							}, 1000);
						} else {
							//debugger;
							uni.showToast({
								title: res.data.StatusMessage,
								duration: 2000,
								icon: 'none'
							});
							//uni.hideToast();
							return;
						}
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		},
		checkMobile: function(mobile) {
			return RegExp(/^1[34578]\d{9}$/).test(mobile);
		},
		checkEmail: function(email) {
			return RegExp(/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/).test(email);
		},
		onNavigationBarButtonTap(e) {
			if (e.type == 'back') {
				uni.reLaunch({
					url: '../user/login'
				});
			}
		}
	},
	onReady() {
		this.initPosition();
	}
};
</script>

<style></style>
